.popup{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 4;
    background-color: rgba(0,0,0, 0.5);
}
.popup-inner{
    position: absolute;
    left: 25%;
    right: 25%;
    top: 12%;
    bottom: 25%;
    margin: auto;
    background: white;
    width: 600px;
    height: 300px;
    border-radius: 12px;
}
